/*アコーディオンタイトル*/
.faq-item__accordion-title {
  position: relative; /*+マークの位置基準とするためrelative指定*/
  cursor: pointer;
  font-size: 1rem;
  font-weight: normal;
  padding: 1rem 6.25rem 1rem 1.5625rem;
  transition: all 0.5s ease;
  color: #fff;
  background: #4682b4;
}
