  position: relative;
  max-width: 85%;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .faq-item__accordion-title-text {
    font-size: 1.25rem;
  }
}

.faq-item__accordion-title-text::before {
  content: "Q.";
  position: absolute;
  top: 0;
  left: -1.125rem;
  color: #fff;
}
@media screen and (min-width: 768px) {
  .faq-item__accordion-title-text::before {
    top: -0.0625rem;
    left: -1.5625rem;
  }
}

/*アイコンの＋と×*/
/* アコーディオンタイトルの疑似要素 */
.faq-item__accordion-title::before,
.faq-item__accordion-title::after {
  position: absolute;
  content: "";
  display: block;
  width: 1.75rem;
  height: 0.0625rem;
  background-color: whitesmoke;
  transition: transform 0.3s ease;
  right: 1.875rem;
  top: 50%;
  transform: translateY(-50%);
}

/* 横線：常に表示 */
.faq-item__accordion-title::before {
  right: 1.875rem;
  top: 50%;
  transform: rotate(0deg);
}

/* 縦線：初期状態では表示（＋）、close時に非表示（−）になる */
.faq-item__accordion-title::after {
  right: 1.875rem;
  top: 50%;
  transform: rotate(90deg);
}

/* クローズ状態（開いているとき）→ 縦線を非表示（回転させて重ねる） */
.faq-item__accordion-title.close::after {
  transform: rotate(180deg);
}

/*アコーディオン全体*/
.faq-item__accordion-area {
  list-style: none;
  margin: 0 auto;
}

/*アコーディオンで現れるエリア*/
