.toc {
  counter-reset: section; /* 連番カウンターの初期化 */
  padding: 1rem;
}

.toc__title {
  font-weight: bold;
  color: #8a2be2;
  text-align: center;
  margin-top: 1.25rem;
  font-size: 1.5rem;
}
@media screen and (min-width: 768px) {
  .toc__title {
    font-size: 2.5rem;
  }
}

.toc__list {
  list-style: none;
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .toc__list {
    margin-top: 2.5rem;
  }
}

.toc__item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
  counter-increment: section; /* 各項目でカウントを進める */
}

.toc__item::before {
  content: counter(section, decimal-leading-zero); /* 01, 02, 03... の形式で表示 */
  width: 2.25rem;
  height: 2.25rem;
  background-color: #8a2be2;
  color: #fff;
  font-weight: bold;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.toc__link {
  font-size: 1.125rem;
  color: #333;
  text-decoration: underline;
  text-decoration-thickness: 0.125rem;
  text-decoration-color: transparent;
  text-underline-offset: 0.25rem;
  transition: text-decoration-color 0.3s ease;
}
@media screen and (min-width: 768px) {
  .toc__link {
    font-size: 1.5rem;
  }
}

.toc__link:hover {
  text-decoration-color: #8a2be2;
}

.top-button {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 1.25rem;
  bottom: 1.25rem;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  border: 0;
  background: none;
  transition: 0.2s;
  letter-spacing: 0.1875rem;
  z-index: 1000;
  color: #111;
}

.top-button span:before {
  position: absolute;
  top: -1.6875rem;
  left: 1.125rem;
  transform: rotate(-45deg);
  display: block;
  width: 1rem;
  height: 1rem;
  content: "";
  border: 0.0625rem solid #111;
  border-width: 0.0625rem 0.0625rem 0 0;
}

.top-button span:after {
  position: absolute;
  width: 2.625rem;
  height: 2.625rem;
  border: 0.0625rem solid #111;
  content: "";
  border-radius: 100%;
  top: -2.6875rem;
  left: 0.3125rem;
}

.is-active {
  opacity: 1;
  visibility: visible;
}

.top-news__list {
  margin-top: 1.875rem;
}

.top-news__btn-wrap.common-btn {
  margin-top: 2.5rem;
  text-align: center;
}

.top-news__tabs {
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .top-news__tabs {
    margin-top: 2.5rem;
  }
}

.works-list__item {
  opacity: 0;
}
@media screen and (min-width: 768px) {
  .works-list__item {
    display: flex;
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .works-list__item:nth-of-type(even) {
    flex-direction: row-reverse;
  }
}

.works-list__item + .works-list__item {
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .works-list__item + .works-list__item {
    margin-top: 2.5rem;
  }
}

.works-list__item-img {
  margin: 0 calc(50% - 50vw);
  width: 100vw;
}
@media screen and (min-width: 768px) {
  .works-list__item-img {
    margin: initial;
    max-width: 53.75rem;
    width: 100%;
  }
}

.works-list__item-img img {
  width: 100%;
  aspect-ratio: 375/280;
  object-fit: cover;
}
@media screen and (min-width: 768px) {
  .works-list__item-img img {
    aspect-ratio: 860/650;
  }
}

.works-list__item-content-bg {
  margin: -1.875rem auto 0;
  padding: 1.875rem 1.25rem;
  position: relative;
  width: 88%;
  background: linear-gradient(135deg, #87cefa 0%, #add8e6 50%, #e0f7ff 100%);
}
@media screen and (min-width: 768px) {
  .works-list__item-content-bg {
    margin-top: 0;
    margin-right: calc(50% - 50vw);
    margin-left: -3.125rem;
    padding: 3.125rem 1.875rem;
    width: 100vw;
  }
}

@media screen and (min-width: 768px) {
  .works-list__item:nth-of-type(even) .works-list__item-content-bg {
    margin-right: -3.125rem;
    margin-left: calc(50% - 50vw);
  }
}

@media screen and (min-width: 768px) {
  .works-list__item-content {
    max-width: 37.5rem;
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .works-list__item:nth-of-type(even) .works-list__item-content {
    margin-left: auto;
  }
}

.works-list__item-title {
  font-size: 1.25rem;
  font-weight: 500;
  letter-spacing: 0.08em;
  color: #fff;
  text-shadow: 0.0625rem 0.0625rem 0.1875rem rgba(0, 0, 0, 0.3);
}
@media screen and (min-width: 768px) {
  .works-list__item-title {
    font-size: 1.5625rem;
  }
}

.works-list__item-text {
  margin-top: 1.875rem;
  font-size: 1rem;
  line-height: 1.5;
  letter-spacing: 0.08em;
  color: #5c4033;
}
@media screen and (min-width: 768px) {
  .works-list__item-text {
    margin-top: 2.5rem;
  }
}

.works-list__item-btn-wrap {
  margin-top: 2.5rem;
}

.works {
  overflow-x: hidden;
}

.works__list {
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .works__list {
    margin-top: 5rem;
  }
}

.wp-pagenavi {
  display: flex;
  gap: 0.375rem;
}
@media screen and (min-width: 768px) {
  .wp-pagenavi {
    gap: 0.75rem;
  }
}

.wp-pagenavi .blog__pagination-arrow,
.wp-pagenavi .current,
.wp-pagenavi .page {
  display: inline-block;
  padding: 0.5rem 0.7rem;
  color: #111;
  border-radius: 0.25rem;
}
@media screen and (min-width: 768px) {
  .wp-pagenavi .blog__pagination-arrow,
  .wp-pagenavi .current,
  .wp-pagenavi .page {
    font-size: 1.5rem;
  }
}

.wp-pagenavi .blog__pagination-arrow,
.wp-pagenavi .page {
  background-color: #eee;
  text-decoration: none;
}

.wp-pagenavi .blog__pagination-arrow:hover,
.wp-pagenavi .page:hover,
.wp-pagenavi .blog__pagination-arrow:focus,
.wp-pagenavi .page:focus {
  background-color: #ddd;
}

.wp-pagenavi .current {
  background-color: #ff8c42;
  color: #fff;
  pointer-events: none;
}

.wp-pagenavi .previouspostslink,
.wp-pagenavi .nextpostslink {
  color: transparent;
  border-radius: 0.25rem;
  position: relative;
}

.wp-pagenavi .previouspostslink::before,
.wp-pagenavi .nextpostslink::before {
  color: #111;
  position: absolute; /* 親を基準に絶対配置 */
  top: 50%; /* 親の高さの50%の位置に移動 */
  left: 50%; /* 親の幅の50%の位置に移動 */
  transform: translate(-50%, -50%); /* 自身のサイズの半分だけ戻して中央揃え */
}
@media screen and (min-width: 768px) {
  .wp-pagenavi .previouspostslink::before,
  .wp-pagenavi .nextpostslink::before {
    font-size: 2rem;
  }
}

.wp-pagenavi .previouspostslink::before {
  content: "<";
}

.wp-pagenavi .nextpostslink::before {
  content: ">";
}